    rgb(218 219 221 / 50%) 87.85%,
    rgb(218 219 221 / 0%) 100%
  );
  --app-store-gradient: linear-gradient(
    0deg,
    rgb(26 26 26 / 10%) -0.24%,
    rgb(33 33 33 / 15.1%) 177.23%,
    rgb(53 53 53 / 22.7%) 444.15%,
    rgb(87 87 87 / 31.8%) 767.35%,
    rgb(133 133 133 / 42.1%) 1129.49%,
    rgb(179 179 179 / 51%) 1442.59%
  );
  --small-shadow: 0 4px 4px rgb(30 33 44 / 3%), 0 12px 10px rgb(154 156 165 / 4%),
    0 30px 24px rgb(154 156 165 / 5%), 0 80px 80px rgb(154 156 165 / 8%);
  --regular-shadow: 0 4px 4px rgb(30 33 44 / 5%), 0 12px 10px rgb(154 156 165 / 8%),
    0 30px 24px rgb(154 156 165 / 10%), 0 80px 80px rgb(154 156 165 / 16%);
  --large-shadow: 0 4px 4px rgb(30 33 44 / 5%), 0 12px 10px rgb(30 33 44 / 8%),
    0 26px 24px rgb(30 33 44 / 10%), 0 60px 80px rgb(30 33 44 / 16%);
  --product-shadow: 0 80px 80px -20px rgb(154 156 165 / 16%),
    0 30px 24px -10px rgb(154 156 165 / 10%), 0 12px 10px -6px rgb(154 156 165 / 8%),
    0 4px 4px -4px rgb(30 33 44 / 5%);
  --main-shadow: 0 4px 4px rgb(0 0 0 / 25%);
  --custom-shadow: 0 8px 4px 4px rgb(0 0 0 / 3%), 0 20px 20px 13px rgb(0 0 0 / 3%);
  --custom-hover-shadow: 0 8px 4px 4px rgb(0 0 0 / 3%), 0 20px 20px 13px rgb(0 0 0 / 3%),
    0 20px 20px 13px rgb(0 0 0 / 3%);
  --custom-top-shadow: 0 5px 10px rgb(0 0 0 / 15%);
