  --z-tooltip: 100;
  --z-postfixed: 11;
  --z-fixed: 10;
  --z-prefixed: 9;
  --z-postup: 2;
  --z-up: 1;
  --z-down: -1;
  --to-right-05-animation: to-right 0.4s ease-in-out forwards 0.5s;
  --to-right-03-animation: to-right 0.4s ease-in-out forwards 0.3s;
  --to-bottom-animation: to-bottom 0.4s ease-in-out forwards 0.5s;
  --scale-in-animation: scale-in 0.4s ease-in-out forwards 0.3s;
  --scale-in-left-animation: scale-in-left 0.4s ease-in-out forwards 0.7s;
  --scale-out-right-animation: scale-out-right 0.4s ease-in-out forwards 0.3s;
}

@keyframes to-right {
  from {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-2.25rem);
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}
@keyframes to-bottom {
  from {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-2.25rem);
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
@keyframes scale-in {
  from {
    opacity: 0;
    visibility: hidden;
    transform: scale(1.15);
    transform-origin: center;
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}
@keyframes scale-in-left {
  from {
    opacity: 0;
    visibility: hidden;
    transform: scale(1.15);
    transform-origin: right center;
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}
@keyframes scale-out-right {
  from {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.85);
    transform-origin: right top;
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  overflow-x: hidden;
  overflow-anchor: none;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  margin-inline: 1.25rem;
  max-width: 76.875rem;
}
@media (min-width: 80.625em) {
  .container {
    margin-inline: auto;
  }
}
@media (max-width: 35.9375em) {
  .container {
    margin-inline: 0.9375rem;
  }
}
