  .container--40 {
    margin-inline: auto;
  }
}
@media (max-width: ) {
  .container--40 {
    margin-inline: 2.5rem;
  }
}
@media (max-width: ) {
  .container--40 {
    margin-inline: 1.875rem;
  }
}
@media (max-width: 35.9375em) {
  .container--40 {
    margin-inline: 0.9375rem;
  }
}
.container--narrow {
  max-width: 50.625rem;
}
@media (min-width: ) {
  .container--narrow {
    margin-inline: auto;
  }
}
@media (max-width: ) {
