@use "../../../scss/util/" as *;

.root {
  display: flex;
  align-items: center;
  width: rem(105);
  white-space: nowrap;
}

.flag {
  position: relative;
  top: rem(1);
  margin-right: rem(12);
  width: rem(20);
  height: rem(12);
}

.selected {
  display: inline-flex;
  align-items: center;
  margin-right: rem(3);
}

.select {
  position: relative;

  &Head {

  }

  &Selected {
    margin-right: rem(4);
    letter-spacing: rem(0.3);

    &Active {

    }
  }

  &Icon {
    position: relative;
    bottom: rem(1);
    width: rem(8);
    height: rem(8);
    fill: currentcolor;
  }

  &Wrapper {
    display: none;
    position: absolute;
    left: 0;
    top: 100%;
  }

  &List {
    list-style: none;
    margin: 0;
    padding: 0;
    background-color: var(--white-color);
  }

  &Item {
    color: var(--gray900-color);
  }
}